
import { formatDate } from "@/common/js/util"
import { callPuExctQryToOICS } from "@/api/api"
import OFooter from "@/components/common/OFooter"
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'


export default {
components: {
Group,
XInput,
XTextarea,
XButton,
ViewBox,
Checklist,
AlertModule,
OFooter
},
data() {
return {
pageData: {},//父页面传入参数
list: [],//页面数据集合
data: {},//页面数据
approveComment: "",//意见说明
approveResultList: [{ key: '1', value: '同意' }, { key: '2', value: '退回' }],
approveResultValue: [],//审核意见
disabledButton: false         }
},
methods: {

replaceValue(value) {
this.approveComment = value.replace(/ /g, '').replace(/\u2006/g, '');
},

getReviewEventDetail() {
let params = {
params: {
RuleName: "GetReviewEventDetail",
Parm: { EventNo: this.pageData.eventNo }
}
};
let callBackSuccess = data => {
if (data.ReturnCd == '00') {
this.list = this.list.concat(data.RspMsg.result.ListItems.ListItem || []);
this.data = this.list[0]
}
else {
this.showModule("获取数据失败", data.RspMsg.msg);
this.disabledButton = true
}
this.$store.commit('updateLoadingStatus', { isLoading: false })
};
callPuExctQryToOICS(params, callBackSuccess);
},//弹出信息提示窗
showModule(title, content) {
AlertModule.show({
title: title,
content: content,
onShow() {

},
onHide() {

}
})
}, showToast(text, width) {
this.$vux.toast.show({
text: text,
type: 'cancel',//success, warn, cancel, text
width: width
})
},//提交预警审核
handleReviewEvent() {
if (this.approveResultValue.length <= 0) { this.showToast('请选择审核意见', '10.0em'); return }
if (this.approveComment == '') { this.showToast('请输入意见说明', '10.0em'); return }

this.$vux.loading.show({ text: '请求中' })
this.disabledButton = true
const _this = this
let params = {
params: {
RuleName: "HandleReviewEvent",
Parm: {
TimePoint: formatDate(new Date(), 'yyyyMMdd')
, EventNo: this.pageData.eventNo
, ApproveResult: this.approveResultValue[0]
, ApproveComment: this.approveComment
}
}
};
let callBackSuccess = data => {
if (data.ReturnCd == '00') {

if (data.RspMsg.result[0].DataFlag == '01') {
_this.$vux.toast.show({
text: '提交成功',
width: '6em',
onHide() {
_this.$router.go(-1)
}
})
}
else {
this.showModule("提交失败", data.RspMsg.msg);
this.disabledButton = false
}
}
else {
this.showModule("提交失败", data.RspMsg.msg);
this.disabledButton = false
}

this.$vux.loading.hide()
};
callPuExctQryToOICS(params, callBackSuccess);
}
},
mounted() {


},
beforeRouteEnter(to, from, next) {
next(vm => {
vm.$store.commit('updateLoadingStatus', { isLoading: true })
vm.list = []
vm.data = {}
vm.approveResultValue = []
vm.approveComment = ''
vm.disabledButton = false
vm.pageData.eventNo = vm.$route.query.eventNo;
vm.getReviewEventDetail()
})
}
}



